@import "variables";
@import "mixins";

body{
  display: grid;
  grid-template-areas: "header"
                       "content";
  grid-template-columns: 1fr;
  font-size: 16px;
  .header{
    grid-area: header;
    .navbar {
      height: 70px;
      .containerParent();
      background-color: #000;
      .container{
        .containerChild();
        .nav-panel{
          display: flex;
          &__logo{
            width: 50%;
            img{
              padding-top: 15px;
            }
          }
          &__menu{
            width: 50%;
            ul {
              display: flex;
              justify-content: space-around;
              li{
                a {
                  font: .875em/4.375em @OpenSS;
                  color: #fff;
                  text-decoration: none;
                  &:hover {
                    color: @hoverColor;
                  }
                }
              }
            }
          }
        }
      }
    }
    &__info{
      .containerParent();
      background: url("../img/header_bg.jpg");
      .container{
        display: flex;
        .containerChild();
        .info{
          width: 50%;
          min-height: 640px;
          img{
            margin-top: 110px;
            width: 175px;
            height: 60px;
          }
          h1{
            margin: 30px 0 30px 0;
            font: 2.25em/1em @RalewayR;
            color: #fff;
            text-transform: uppercase;
          }
          span{
            color: #fff;
          }
          p{
            margin: 40px 0 45px 0;
            font: 1em/1.2em @RalewayL;
            color: #fff;
          }
          a{
            display: block;
            width: 135px;
            height: 45px;
            font: .938em/45px @OpenSS;
            color: #fff;
            text-align: center;
            border: 1px solid #fff;
            text-decoration: none;
            &:hover{
              background-color: @hoverColor;
              border-color: @hoverColor;
            }
          }
        }
        .form{
          width: 50%;
          position: relative;
          div.form__window {
            width: 360px;
            position: absolute;
            top: 150px;
            left: 25%;
            .headline {
              height: 55px;
              font: 1.25em/55px @OpenSB;
              text-align: center;
              color: #000;
              background-color: @bgColor1;
              border-top-left-radius: 5px;
              border-top-right-radius: 5px;
              span {
                color: @hoverColor;
              }
            }
            p{
              height: 85px;
              background-color: #fff;
              position: relative;
              label{
                input{
                  width: 280px;
                  height: 30px;
                  position: absolute;
                  left: 10%;
                  top: 35%;
                  border: 1px solid transparent;
                  border-bottom-color: #d6d6d6;
                  outline: none;
                  &:focus{
                    border-color: @hoverColor
                  }
                }
              }
            }
            a {
              display: block;
              height: 55px;
              font: .938em/55px @OpenSS;
              text-align: center;
              color: #fff;
              text-decoration: none;
              background-color: #ff9b51;
              border-bottom-left-radius: 5px;
              border-bottom-right-radius: 5px;
              &:hover {
                background-color: @hoverColor;
              }
            }
          }
        }
      }
    }
    .social{
      .containerParent();
      position: relative;
      z-index: 1;
      box-shadow: 0 5px 10px rgba(183, 183, 183, .6);
      .container{
        display: flex;
        .containerChild();
        .social__info{
          width: 30%;
          padding-top: 30px;
          h2{
            font: 1.25em/1em @OpenSB;
          }
          p{
            font: .875em/1em @OpenS;
            color: @mainFontColor;
          }
        }
        .social__links{
          width: 70%;
          ul{
            display: flex;
            justify-content: space-around;
            li{
              a{
                color: #cccccc;
                i{
                  font-size: 3em;
                  line-height: 105px;
                }
                &:hover{
                  color: @hoverColor;
                }
              }
            }
          }
        }
      }
    }
  }
  .content {
    grid-area: content;
    .tabs{
      background-color: @bgColor1;
      .containerParent();
      .container{
        .containerChild();
        .marginForContainer();
        display: flex;
        justify-content: space-around;
        .tabs__selector{
          width: 15%;
          display: flex;
          flex-direction: column;
          align-items: center;
          ul{
            li{
              a {
                display: block;
                width: 110px;
                height: 110px;
                font: .938em/110px @OpenSB;
                text-align: center;
                color: #fff;
                background-color: #333;
                text-decoration: none;
                cursor: pointer;
                &:hover {
                  background-color: #000;
                }
              }
            }
          }
          li:nth-child(2){
            border-top: 1px solid @bgColor1;
            border-bottom: 1px solid @bgColor1;
          }
        }
        .tabs__descr{
          width: 50%;
          padding: 0 50px;
          display: flex;
          flex-direction: column;
          align-items: center;
          div{
            h3{
              font: 1.25em/1.25em @OpenSB;
            }
            span{
              display: block;
              margin: 15px 0 25px 0;
              color: #ff9b51;
            }
            p{
              margin: 15px 0 15px 0;
              font: .875em/1.75em @RalewayR;
              color: @mainFontColor
            }
            a{
              margin-top: 35px;
              display: block;
              width: 120px;
              height: 45px;
              font: .938em/45px @OpenSB;
              text-align: center;
              color: #fff;
              text-decoration: none;
              background-color: #ff9b51;
              border-radius: 5px;
              &:hover{
                background-color: @hoverColor;
              }
            }
            &:nth-child(n+2){
              display: none;
            }
          }
        }
        .tabs__img{
          margin-top: 50px;
          width: 35%;
          display: flex;
          flex-direction: column;
          align-items: center;
        }
      }
    }
    .subList{
      .containerParent();
      .container{
        .containerChild();
        .marginForContainer();
        display: flex;
        justify-content: space-between;
        .subList__img{
          width: 50%;
          display: flex;
          flex-direction: column;
          img{
            margin: 25px 0 0 55px;
            width: 70%;
            box-shadow: 10px 10px 50px #cecece;
          }
        }
        .subList__section{
          width: 50%;
          padding: 0 50px;
          display: flex;
          flex-direction: column;
          h3{
            font: 1.25em/1.25em @OpenSB;
          }
          span{
            display: block;
            margin: 15px 0 25px 0;
            color: #ff9b51;
          }
          p {
            margin: 15px 0 15px 0;
            font: .875em/1.75em @RalewayR;
            color: @mainFontColor
          }
          ul{
            li{
              display: flex;
              margin: 40px 0;
              i{
                width: 70px;
                height: 60px;
                margin-right: 50px;
                font-size: 2em;
                line-height: 60px;
                color: #ff9b51;
                text-align: center;
                border: 3px solid #ff9b51;
                border-radius: 50px;
              }
              .subList__descr{
                width: 91%;
                h4{
                  font: .875em/1.75em @OpenSS;
                }
                p{
                  margin: 5px 0;
                }
              }
            }
          }
        }
      }
    }
    .standartPicture{
      background: @bgColor1;
      .containerParent();
      .container{
        .containerChild();
        .marginForContainer();
        display: flex;
        justify-content: space-between;
        .standartPicture__section{
          width: 50%;
          padding: 0 50px;
          display: flex;
          flex-direction: column;
          h3{
            font: 1.25em/1.25em @OpenSB;
          }
          span{
            display: block;
            margin: 15px 0 25px 0;
            color: #ff9b51;
          }
          p {
            margin: 15px 0;
            font: .875em/1.75em @RalewayR;
            color: @mainFontColor
          }
        }
        .standartPicture__img{
          width: 50%;
          display: flex;
          flex-direction: column;
          img{
            margin-left: 55px;
            width: 70%;
            box-shadow: 10px 10px 50px #cecece;
          }
        }
      }
    }
    .whyAwesome,
    .pricingOptions,
    .customersReview,
    .stylish{
      .containerParent();
      .container{
        .containerChild();
        .marginForContainer();
        .whyAwesome__headline,
        .pricingOptions__headline,
        .customersReview__headline{
          display: flex;
          flex-direction: column;
          align-items: center;
          text-align: center;
          h2{
            font: 2.25em/2.25em @RalewayR;
            text-transform: uppercase;
          }
          span{
            display: block;
            margin: 5px 0 15px 0;
            color: #ff9b51;
          }
          p {
            margin: 15px 0;
            font: .875em/1.75em @RalewayR;
            color: @mainFontColor;
          }
        }
        .whyAwesome__advantages{
          display: flex;
          div{
            width: 33.3%;
            padding: 0 25px;
            margin-top: 80px;
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
            i{
              width: 60px;
              height: 60px;
              font-size: 2em;
              line-height: 60px;
              color: #ff9b51;
              text-align: center;
              border: 3px solid #ff9b51;
              border-radius: 50px;
            }
            h4{
              font: 1.25em/4em @OpenSB;
            }
            p {
              font: .875em/1.75em @RalewayR;
              color: @mainFontColor
            }
          }
        }
      }
    }
    .pricingOptions{
      background: @bgColor1;
      .pricingOptions__advantages{
        display: flex;
        align-items: flex-end;
        div{
          width: 30%;
          margin: 80px 25px 0 25px;
          display: flex;
          flex-direction: column;
          align-items: center;
          text-align: center;
          background: #333;
          h4{
            width: 100%;
            font: 1.25em/3.75em @OpenSB;
            color: #fff;
            border-bottom: 1px solid @mainFontColor;
          }
          p{
            width: 100%;
            font: 2.2em/2.2em @OpenSB;
            color: #fff;
            background: #333;
            span{
              display: inline;
              font: 2.5em/1.5em @OpenSS;
              color: #fff;
              border-bottom: none;
            }
          }
          span{
            display: inline-block;
            width: 100%;
            font: .875em/3em @OpenSS;
            color: @mainFontColor;
            border-bottom: 2px solid #ff8b38;
          }
          .brdBottom{
            color: #fff;
            text-transform: uppercase;
            background-color: #ff8b38;
          }
          ul{
            width: 100%;
            background-color: #fff;
            li{
              font: .875em/3em @OpenS;
              color: @mainFontColor;
              text-transform: uppercase;
              border-bottom: 1px solid @bgColor1;
            }
          }
        }
      }
    }
    .customersReview{
      .customersReview__review{
        margin-top: 60px;
        display: flex;
        justify-content: space-around;
        div{
          width: 20%;
          p{
            padding: 25px 15px;
            position: relative;
            font: .875em/1.35em @OpenS;
            color: #86878b;
            background-color: #f5f5f5;
            border-radius: 5px;
            &:after{
              content: " ";
              position: absolute;
              left: 40px;
              bottom: -40px;
              border: 20px solid transparent;
              border-top: 20px solid #f5f5f5;
            }
          }
          .customer{
            display: flex;
            align-items: center;
            margin-top: 30px;
            width: 100%;
            img{
              margin: 0 25px;
            }
            ul{
              li{
                &:nth-child(1){
                  font: 1.25em/1.5em @OpenSB;
                }
                &:nth-child(2){
                  font: .875em/1.5em @OpenS;
                  color: #86878b;
                }
              }
            }
          }
        }
      }
    }
    .stylish{
      background: url("../img/stylish_bg.jpg") no-repeat #000;
      background-size: cover;
      .stylish__headline{
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        h2{
          font: 2.25em/2.25em @RalewayR;
          color: #fff;
          text-transform: uppercase;
        }
        span{
          display: block;
          margin: 5px 0 15px 0;
          color: #fff;
        }
        p {
          margin: 15px 0;
          font: .875em/1.75em @RalewayR;
          color: #fff;
        }
        a{
          display: block;
          margin-top: 35px;
          width: 135px;
          height: 40px;
          font: .938em/40px @OpenSS;
          color: #fff;
          text-decoration: none;
          border: 1px solid #fff;
          &:hover{
            background-color: @hoverColor;
            border-color: @hoverColor;
          }
        }
      }
    }
  }
}